/* src/components/ChapterReaderPage.css */
.container {
    display: flex;
    flex-direction: column;
    align-items: flex-start; /* Align items to the start (left) */
    justify-content: center;
    font-family: Arial, sans-serif;
    margin: 0 3%;
    padding: 3%;
    /*padding: 40px; /* Add padding to the container */
    /*margin: 0 40px; /* Increase margin from the left and right */
  }
  
  button {
    margin: 0 5px;
    padding: 5px 10px;
    font-size: 16px;
    cursor: pointer;
  }
  
  h1 {
    text-align: left; /* Ensure the title is also left-aligned */
    width: 100%; /* Make the title full width for left alignment */
  }
  
  p {
    margin-top: 20px;
    margin-bottom: 20px;  /* Add spacing between the title and content */
    text-align: left; /* Ensure paragraph text is left-aligned */

  }
  