/* src/components/HomePage.css */
.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: Arial, sans-serif;
    text-align: center;
    padding: 20px;
  }
  
  .novel-list {
    list-style-type: none;
    padding: 0;
  }
  
  .novel-list li {
    margin: 10px 0;
  }
  
  .novel-list a {
    text-decoration: none;
    color: #007bff; /* Link color */
  }
  