/* src/components/ChapterListPage.css */
.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: Arial, sans-serif;
    text-align: left;
    padding: 20px;
  }
  
  .chapter-list {
    list-style-type: none;
    padding: 0;
  }
  
  .chapter-list li {
    margin: 10px 0; 
    padding: 5px;
  }
  
  .chapter-list a {
    text-decoration: none;
    color: #1c4372; /* Link color */
  }
  